"use client"
import { useState, useEffect } from "react"
import { motion } from "framer-motion"

export default function AppointmentForm({ onClose, onSubmit, initialData }) {
  const [formData, setFormData] = useState({
    paciente_id: '',
    doctor_id: '',
    fecha_hora: '',
    estado_id: '1',
    notas: ''
  })

  const [pacientes, setPacientes] = useState([])
  const [doctores, setDoctores] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [pacientesRes, doctoresRes] = await Promise.all([
          fetch('/api/pacientes'),
          fetch('/api/doctores')
        ])

        if (!pacientesRes.ok || !doctoresRes.ok) {
          throw new Error('Error al cargar los datos')
        }

        const [pacientesData, doctoresData] = await Promise.all([
          pacientesRes.json(),
          doctoresRes.json()
        ])

        setPacientes(pacientesData)
        setDoctores(doctoresData)
      } catch (error) {
        setError(error.message)
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [])

  useEffect(() => {
    if (initialData) {
      setFormData({
        ...initialData,
        fecha_hora: new Date(initialData.fecha_hora).toISOString().slice(0, 16)
      })
    }
  }, [initialData])

  const handleSubmit = (e) => {
    e.preventDefault()
    onSubmit(formData)
  }

  const handleChange = (e) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
  }

  if (loading) return <div>Cargando...</div>
  if (error) return <div className="text-red-500">{error}</div>

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="block text-sm font-medium text-gray-700">
          Paciente
        </label>
        <select
          name="paciente_id"
          value={formData.paciente_id}
          onChange={handleChange}
          required
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-sky-500 focus:ring-sky-500"
        >
          <option value="">Seleccionar paciente</option>
          {pacientes.map(paciente => (
            <option key={paciente.paciente_id} value={paciente.paciente_id}>
              {paciente.primer_nombre} {paciente.apellido_paterno}
            </option>
          ))}
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">
          Doctor
        </label>
        <select
          name="doctor_id"
          value={formData.doctor_id}
          onChange={handleChange}
          required
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-sky-500 focus:ring-sky-500"
        >
          <option value="">Seleccionar doctor</option>
          {doctores.map(doctor => (
            <option key={doctor.doctor_id} value={doctor.doctor_id}>
              Dr. {doctor.primer_nombre} {doctor.apellido_paterno} - {doctor.especialidad}
            </option>
          ))}
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">
          Fecha y Hora
        </label>
        <input
          type="datetime-local"
          name="fecha_hora"
          value={formData.fecha_hora}
          onChange={handleChange}
          required
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-sky-500 focus:ring-sky-500"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">
          Estado
        </label>
        <select
          name="estado_id"
          value={formData.estado_id}
          onChange={handleChange}
          required
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-sky-500 focus:ring-sky-500"
        >
          <option value="1">Programada</option>
          <option value="2">Confirmada</option>
          <option value="3">Cancelada</option>
          <option value="4">Completada</option>
          <option value="5">No asistió</option>
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">
          Notas
        </label>
        <textarea
          name="notas"
          value={formData.notas}
          onChange={handleChange}
          rows="3"
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-sky-500 focus:ring-sky-500"
        />
      </div>

      <div className="flex justify-end space-x-3">
        <motion.button
          whileHover={{ scale: 1.05 }}
          type="button"
          onClick={onClose}
          className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50"
        >
          Cancelar
        </motion.button>
        <motion.button
          whileHover={{ scale: 1.05 }}
          type="submit"
          className="px-4 py-2 text-sm font-medium text-white bg-sky-500 border border-transparent rounded-md hover:bg-sky-600"
        >
          {initialData ? 'Actualizar' : 'Crear'} Cita
        </motion.button>
      </div>
    </form>
  )
} 